            </van-swipe-item>
          </van-swipe>
        </div>

        <!-- 王淑岩轮播图区域 -->
        <!-- 王淑岩上部导航区域 -->
        <div class="wsy_kinds">
          <ul>
            <router-link
              tag="li"
              :to="item.path"
              v-for="(item, index) in wsy_nav"
              :key="index"
            >
              <img :src="item.img" alt />
              <p>{{ item.title }}</p>
            </router-link>
          </ul>
        </div>
        <!-- 王淑岩上部导航区域 -->
        <div class="wsy_list" v-for="(item, index) in list" :key="index">
          <!-- 名师阵容区 -->
          <template v-if="item.channel_info.type == 3">
            <p class="title">{{ item.channel_info.name }}</p>
            <div class="wsy_content">
              <div
                class="wsy_item"
                @click="toTeacherInfo(item.teacher_id)"
                v-for="(item, index) in item.list"
                :key="index"
              >
                <img :src="item.teacher_avatar" />
                <div>
                  <p>{{ item.teacher_name }}</p>
                  <p>{{ item.introduction }}</p>
                </div>
              </div>
            </div>
          </template>
          <!-- 名师阵容区 -->
          <!-- 精品课程区 -->
          <template v-if="item.channel_info.type == 1">
            <p class="title">{{ item.channel_info.name }}</p>
            <div class="wsy_content">
              <div
                class="wsy_ii_item"
                @click="ToCourseDetail(item.id, item.course_type)"
                v-for="(item, index) in item.list"
                :key="index"
              >
                <p>{{ item.title }}</p>
                <div class="wsy_ii_time">
                  <p>共{{ item.total_periods | periods }}课时</p>
                </div>
                <div
                  class="wsy_ii_teacher"
                  v-for="(it, i) in item.teachers_list"
                  :key="i"
                >
                  <img :src="it.teacher_avatar" />
                  <p>{{ it.teacher_name }}</p>
                </div>

                <div class="wsy_ii_info">
                  <span>{{ item.sales_num }}人已报名</span>
                  <span class="wsy_good" v-show="item.price == 0">免费</span>
                  <span class="wsy_price" v-show="item.price != 0">
                    <img
                      src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png"
                    />
                    <span>{{ item.price | toFixed }}</span>
                  </span>
                </div>
                <img
                  v-show="item.has_buy != 0"
                  class="wsy_flag_img"
                  src="https://wap.365msmk.com/img/has-buy.6cfbd83d.png"
                  alt
                />
              </div>
            </div>
          </template>
          <!-- 精品课程区 -->
          <!-- 推荐课程区 -->
          <!-- <template  v-if='(item.channel_info.type == 1)'>
            <p class="title">{{ item.channel_info.name }}</p>
            <div class="wsy_content">
              <div
                class="wsy_ii_item"
                @click="ToCourseDetail(item)"
                v-for="(item,index) in item.list"
                :key="index"
              >
                <p>{{ item.title }}</p>
                <div class="wsy_ii_time">
                  <p>共{{ item.total_periods | periods }}课时</p>
                </div>
                <div
                  class="wsy_ii_teacher"
                  v-for="(it, i) in item.teachers_list"
                  :key="i"
                >
                  <img :src="it.teacher_avatar" />
                  <p>{{ it.teacher_name }}</p>
                </div>

                <div class="wsy_ii_info">
                  <span>{{ item.sales_num }}人已报名</span>
                  <span class="wsy_good" v-show="item.price == 0">免费</span>
                  <span class="wsy_price" v-show="item.price != 0">
                    <img
                      src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png"
                    />
                    <span>{{ item.price }}</span>
                  </span>
                </div>
                <img
                  v-show="!item.has_buy"
                  class="wsy_flag_img"
                  src="https://wap.365msmk.com/img/has-buy.6cfbd83d.png"
                />
              </div>
            </div>
          </template>-->
          <!-- 推荐课程区 -->
          <!-- 名师讲师区 -->
          <!-- <template  v-if='(item.channel_info.type == 3)'>
            <p class="title">{{ item.channel_info.name }}</p>
            <div class="wsy_content">
              <div
                class="wsy_item"
                @click="toTeacherInfo(item.teacher_id)"
                v-for="(item,index) in item.list"
                :key="index"
              >
                <img :src="item.teacher_avatar" />
                <div>
                  <p>
                    {{ item.teacher_name }}
                    <span>M10</span>
                  </p>
                  <p>{{ item.introduction }}</p>
                </div>
              </div>
            </div>
          </template>-->
          <!-- 名师讲师区 -->
        </div>
      </div>
    </div>
    <!-- <WsyLogin></WsyLogin> -->
    <WsyLogin v-if="this.$store.state.wsy_isClose"></WsyLogin>
  </div>
</template>

<script>
// import Section from "@/components/Wsy_section.vue";
import WsyLogin from "@/components/wsy_login";
import BetterScroll from "better-scroll";
export default {
  name: "wsy_index",
  components: {
    WsyLogin,
  },
  props: {},
  data() {
    return {
      // better-scroll实例
      bs: null,
      index: 1,
      bannerList: [], //轮播图数据
      //   导航区域数据
      wsy_nav: [
        {
          title: "特色课",
          path: "/lwh_course",
          img:
            "data:image/png;base64,",
        },
        {
          title: "一对一辅导",
          path: "/oto",
          img:
            "data:image/png;base64,",
        },
        {
          title: "学习日历",
          path: "/StudyCalendar",
          img:
            "data:image/png;base64,",
        },
      ],
      // 名师阵容区域数据
      famousTeacher: {},
      // 精品课程数据
      excellentCourse: {},
      // 推荐课程数据
      recommendedCourse: {},
      // 明星讲师
      starLecturer: {},
      list: [], //列表数据
    };
  },
  created() {},
  mounted() {
    this.getBanner();
    this.getIndex();
    this.bs = new BetterScroll(this.$refs.scrollBox, {
      probeType: 3,
      click: true,
    });
    this.bs.on("scroll", (pos) => {
      if (pos.y >= 0) {
        this.bs.scrollTo(0, 0);
      }
    });
    let token = sessionStorage.getItem("token");
    if (token) {
      this.$store.commit("wsy_Close", false);
    }
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    console.log(to, from);

    // if(from.path == "/lwh_main" && to.path == "/StudyCalendar"){
    //     this.$store.commit("wsy_Close",true)
    //     next(false)
    // }
    next();
  },
  methods: {
    //   点击进入课程详情页面
    ToCourseDetail(id, course_type) {
      // window.localStorage.setItem("CourseInfo", JSON.stringify(info));
      this.$router.push({
        path: "/CourseDetail",
        query: {
          id,
          course_type,
        },
      });
    },
    // 获取轮播图数据
    async getBanner() {
      let { data } = await this.$http.get("/api/app/banner");
      this.bannerList = data.data;
    },
    // 获取首页列表数据
    async getIndex() {
      let { data } = await this.$http.get("/api/app/recommend/appIndex");
      this.list = data.data;
      // this.famousTeacher = data.data[4];
      // this.excellentCourse = data.data[1];
      // this.recommendedCourse = data.data[3];
      // this.starLecturer = data.data[0];
      console.log(this.list);
    },
    // 点击名师阵容跳转到讲师详情
    toTeacherInfo(id) {
      let token = sessionStorage.getItem("token");
      if (token) {
        this.$router.push(`/teacher?id=${id}`);
      } else {
        this.$store.commit("wsy_Close", true);
      }
    },
  },
  filters: {
    periods(val) {
      switch (val) {
        case 0:
          return "零";
          break;
        case 1:
          return "一";
          break;
        case 2:
          return "二";
          break;
        case 3:
          return "三";
          break;
        case 4:
          return "四";
          break;
        case 5:
          return "五";
          break;
        case 6:
          return "六";
      }
    },
  },
  computed: {},
  watch: {
    list() {
      this.$nextTick(() => {
        this.bs.refresh();
      });
    },
  },
};
</script>


<style lang="scss" scoped>
